* {
    box-sizing: border-box;
    padding: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 0;
}

.main {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.search {
    margin: 12px auto;
    position: relative;
    /* border: 2px solid black; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.search input {
    border: none;
    border: 2px solid blue;
    position: relative;
    padding-left: 25px;
    border-radius: 30px;
    width: 400px;
    font-size: 20px;
    height: 50px;
}

.search button {
    height: 50px;
    width: 120px;
    position: absolute;
    margin-left: 280px;
    border-radius: 30px;
    border: none;
    border: 2px solid blue;
    background-color: rgb(0, 106, 255);
    color: white;
    font-size: 18px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.weatherCard {
    /* border: 2px solid black; */
    width: 750px;
    position: relative;
    /* height: 300px; */
    padding: 25px;
    border-radius: 35px;
    background-image: linear-gradient(rgb(0, 106, 255), rgb(126, 180, 255));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.weatherCarderr {
    /* border: 2px solid black; */
    width: 750px;
    position: relative;
    /* height: 300px; */
    padding: 10px;
    border-radius: 35px;
    background-image: linear-gradient(rgb(255, 0, 0), rgb(255, 118, 118));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.weatherCarderr h3 {
    font-size: 40px;
}

button:hover {
    cursor: pointer;
}

.weatherCard h1 {
    font-size: 80px;
    color: white;
    display: flex;
    justify-content: center;
    gap: 0;
}

.weatherCard h3 {
    font-size: 30px;
}

.weatherCard h4 {
    color: white;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.weatherCard p {
    font-size: 25px;
}

.det {
    /* border: 1px solid white; */
    width: 98%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 18px 0px;
}

.det1 span {
    font-weight: normal;
    border: 2px solid blue;
    display: flex;
    border-radius: 12px;
    background-color: blue;
    color: white;
    padding: 0px 5px;
    width: fit-content;
    align-items: center;
}

.det1 h5 {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid white;
}

.det1 {
    /* border: 2px solid red; */
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.indet span {
    font-weight: normal;
    border: 2px solid blue;
    display: flex;
    border-radius: 12px;
    background-color: blue;
    color: white;
    padding: 0px 5px;
    width: fit-content;
    align-items: center;
}

.indet h5 {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid white;
}

.indet {
    /* border: 2px solid red; */
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 18px;
}

.det2 {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.det2 p {
    font-size: 18px;
    font-weight: bold;
}

.detBut {
    border: none;
    background-color: rgba(255, 255, 255, 0);
    position: absolute;
    bottom: 0;
    font-size: 23px;
}

@media (max-width:480px) {
    * {
        box-sizing: border-box;
        padding: 0;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        margin: 0;
    }

    .weatherCard {
        width: 400px;
    }
    .search input {
        height: 30px;
        font-size: 12px;
        width: 300px;
    }
    .search button{
        height: 30px;
        width: 80px;
        font-size: 12px;
        margin-left: 220px;
    }
    .weatherCard h1 {
        font-size: 50px;
    }
    .weatherCard h2 {
        font-size: 20px;
    }
    
    .weatherCard h3 {
        font-size: 20px;
    }
    
    .weatherCard h4 {
        color: white;
        font-size: 15px;
    }
    
    .weatherCard p {
        font-size: 15px;
    }
    .image{
        height: 55px;
        width: 55px;
    }
    /* ---------------- */
    .det {
        /* border: 1px solid white; */
        width: 98%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 45px 0px 20px 0px;
    }
    
    .det1 span {
        font-weight: normal;
        border: 2px solid blue;
        display: flex;
        border-radius: 12px;
        background-color: blue;
        color: white;
        height: fit-content;
        font-size: 10px;
        padding: 0px 5px;
        width: fit-content;
        align-items: center;
    }
    
    .det1 h5 {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgba(255, 255, 255, 0);
    }
    
    .det1 {
        /* border: 2px solid red; */
        width: 50%;
        display: flex;
        flex-direction: column;
        gap: 12px;
        font-size: 13px;
    }
    
    .indet span {
        font-weight: normal;
        border: 2px solid blue;
        display: flex;
        height: fit-content;
        font-size: 10px;
        border-radius: 12px;
        background-color: blue;
        color: white;
        padding: 0px 5px;
        width: fit-content;
        align-items: center;
    }
    
    .indet h5 {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgba(255, 255, 255, 0);
        font-size: 12px;
    }
    
    .indet {
        /* border: 2px solid red; */
        display: flex;
        width: 100%;
        flex-direction: column;
        gap: 18px;
    }
    
    .det2 {
        width: 40%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
    }
    
    .det2 p {
        font-size: 15px;
        font-weight: bold;
    }
    
}